<template>
	<div v-theme:column="'wide'" id="show-blogs">
		<h1>博客总览</h1>
		<input type="text" v-model="search" placeholder="search" />
		<div v-for="blog of filteredBlogs" class="single-blog">
			<router-link v-bind:to=" '/blog/' + blog.objectId">
				<h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
			</router-link>
			<article>{{blog.content | snippet}}</article>
		</div>
	</div>
</template>

<script>
export default{
	name:'show-blogs',
	data(){
		return{
			blogs:[],  //定义空数组，存放从jsonplaceholder传来的10条数据
			search:""
		}
	},
	created(){
		const query=Bmob.Query("Blog");
		query.find().then(res=>{
			console.log(res);
			this.blogs=res;
		})
	},
	computed:{
		filteredBlogs:function(){
			return this.blogs.filter((blog)=>{
				return blog.title.match(this.search.toLowerCase());
			})
		}
	}
}
</script>

<style scoped>
router-link:hover {
	color: darkgray;
}
router-link:active {
	color: darkgray;
}
#show-blogs{
	max-width: 800px;
	margin: 0 auto;
}
.single-blog{
	padding: 20px;
	margin: 20px auto;
	box-sizing: border-box;
	background:#A9A9A9;
	border: 1px dotted #AAAAAA;
}
#show-blogs a{
	color: #A9A9A9;
	text-decoration: none;
}
input[type="text"]{
	padding: 8px;
	width: 80%;
	box-sizing: border-box;
}
</style>



