<template>
	<div id="add-blog">
		<h2>添加博客</h2>
		<h2>爱你哦！黄亚婷大宝贝</h2>
		<form v-if="!submiited">
			<label>博客标题</label>
			<input type="text" v-model="blog.title" required />  <!--required 属性规定必需在提交之前填写输入字段。-->
		
		    <label>博客内容</label>
			<textarea v-model="blog.content"></textarea>
			
			<div id="checkboxes">
				<label>科技</label>
				<input type="checkbox" value="科技" v-model="blog.categories" />
				<label>历史</label>
				<input type="checkbox" value="历史" v-model="blog.categories" />
				<label>军事</label>
				<input type="checkbox" value="军事" v-model="blog.categories" />
				<label>文学</label>
				<input type="checkbox" value="文学" v-model="blog.categories" />
				<label>诗歌</label>
				<input type="checkbox" value="诗歌" v-model="blog.categories" />
				<label>音乐</label>
				<input type="checkbox" value="音乐" v-model="blog.categories" />
				<label>散文</label>
				<input type="checkbox" value="散文" v-model="blog.categories" />
				<label>小说</label>
				<input type="checkbox" value="小说" v-model="blog.categories" />
			</div>
			<label>作者:</label>
			<select v-model="blog.author">
				<option v-for="author of authors">{{author}}</option>
			</select>
			<button v-on:click.prevent="Post">添加博客</button>
		</form>
		
		<div v-if="submiited">
			<h3>博客发布成功!</h3>
		</div>
		
		<div id="preview">
			<h3>博客总览</h3>
			<p>博客标题:{{blog.title}}</p>
			<p>博客内容:{{blog.content}}</p>
			<p>博客分类:</p>
			<ul>
				<li v-for="category of blog.categories">{{category}}</li>
			</ul>
			<p>作者:{{blog.author}}</p>
		</div>
	</div>
</template>

<script>
export default{
	name: 'add-blog',
	data () {
		return{
			blog:{
				title:"",
				content:"",
				categories: [],
				author:""
				
			},
			authors:["黄亚婷狗宝贝","吴庆捷大宝贝","吴英俊","黄冰冰","黄美丽"],
			submiited: false
		}
	},
	methods:{
		Post:function(){
			var con = confirm("确定提交？");
			if(!con){
				return;
			}
			const query=Bmob.Query("Blog");
			query.set("title",this.blog.title);
			query.set("content",this.blog.content);
			query.set("categories",this.blog.categories);
			query.set("author",this.blog.author);
			query.save().then(res=>{
				console.log(res);
				this.submiited=true;
			}).catch(err=>{
				console.log(err);
			})
		}
	}
}
		
</script>

<style scoped>
#add-blog *{
	box-sizing: border-box;
}
#add-blog{
	margin: 20px auto;
	max-width: 600px;
	padding: 20px;
}
label{
	display: block;
	margin: 20px 0 10px;
}
input[type="text"] ,textarea ,select{
	display: block;
	width: 100%;
	padding: 8px;
	font-size:larger;
	font-weight: 400;
	font-style: initial;
}

#checkboxes label{
	display: inline-block;  /*inline-block可以与其它元素排列在同一行*/
	margin-top: 0;
	
}
#checkboxes input{
	display: inline-block;
	margin-right: 10px;
}
button{
	display: block;
	margin: 20px 0;
	background: crimson;
	color: white;
	border: 0;
	padding: 14px;
	border-radius: 4px;
	font-size: 18px;
	cursor: pointer;
}
#preview{
	padding: 10px 20px;
	border: 2px dotted darkgray;
	margin: 30px 0;
}
h3{
	margin-top: 10px;
	color:darkgrey;
}
textarea{
	height: 200px;
	width: 90%;
	font-size:larger;
	font-weight: 400;
	font-style: initial;
}
select{
	width: 40%;
	height: 40px;
	font-size:larger;
	font-weight: 400;
	font-style: initial;
}
</style>



