<template>
	<div id="single-blog">
		<h1>{{blog.title}}</h1>
		<h3>作者: {{blog.author}}</h3>
		<h3>
			分类:
			<span v-for="category of blog.categories">{{category}}*</span>
		</h3>
		<article>{{blog.content}}</article>
		<button @click="deleteSingleBlog()">删除</button>
		<router-link v-bind:to=" '/edit/' + id">编辑</router-link>
	</div>
</template>

<script>
export default{
	name:"single-blog",
	data(){
		return{
			id:this.$route.params.id,
			blog:{}
		 }
		},
		created(){
			const query=Bmob.Query("Blog");
			query.get(this.id).then(res=>{
				console.log(res);
				this.blog=res;
			}).catch(err=>{
				console.log(err);
			})
		},
		methods:{
			deleteSingleBlog:function(){
				const query=Bmob.Query('Blog');
				query.destroy(this.id).then(res=>{
					console.log(res);
					this.$router.push({path:'/'})
				}).catch(err=>{
					console.log(err);
				})
			}
		}
}
</script>

<style scoped>
#single-blog{
	max-width: 960px;
	margin: 0 auto;
	padding: 20px;
	background: #A9A9A9;
	border: 1px dotted #aaa;
}
span{
	display: inline-block;
	font-size: medium;
}
article{
	
	overflow: auto;
	
}

</style>



