<template>
	<nav>
		<ul>
			<li>
				<router-link to="/" exact="">博客首页</router-link>
				<router-link to="/add" exact="">写博客</router-link>
			</li>
		</ul>
	</nav>
</template>

<script>
	export default{
		name:"blog-header"
	}
</script>

<style scoped>
ul{
	list-style-type: none;
	text-align: center;
	margin: 0 auto;
}
li{
	display: inline-block;
	margin: 0 20px;
	
}
a{
	color: #FFFFFF;
	text-decoration: none;
	padding: 12px;
	border-radius: 5px;
}
nav{
	background: crimson;
	padding: 30px 0;
	margin-bottom: 20px;
}
.router-link-active{
	background: rgba(255,255,255,0.8);
	color: #A9A9A9;
}
</style>



